import React, { useEffect } from "react";

const Comments = () => {
  useEffect(() => {
    // Dynamically load Artalk JavaScript
    const script = document.createElement("script");
    script.src = "https://clownfish-app-hd3eu.ondigitalocean.app/dist/Artalk.js";
    script.async = true;

    script.onload = () => {
      // Initialize Artalk after the script has loaded
      if (window.Artalk) {
        window.Artalk.init({
          el: "#comment-container",
          server: "https://clownfish-app-hd3eu.ondigitalocean.app", // Replace with your server URL
          site: "YourSiteName", // Replace with your site name
        });
      }
    };
    document.body.appendChild(script);

    // Cleanup the script if the component unmounts
    return () => {
      document.body.removeChild(script);
    };
  }, []);

  return <div id="comment-container"></div>;
};

export default Comments;